      switch(roleCode) {
        case '0':
          return '本人'
          break;
        case '1':
          return '配偶'
          break;
        case '2':
          return '子女'
          break;
        case '3':
          return '父母'
          break;
        case '4':
          return '其他'
          break;
        case '5':
          return '孫子女'
          break;
        case '6':
          return '戶長'
          break;
        default:
          return roleCode
      }
    },
  },
  components: { 
    Avatar, CustomerInfo, TabHeader, // basic component
    FamilyModal, WarningModal, AddFamilyModal },
  computed: {
    ...mapGetters(['BaseUrl' , 'CustInfo'])
  },
  methods: {
    ...mapActions([
      'setPageLock' , 'updateCustID' , 'updateCustFamilyLeader'
    ]),
    createFamily(){
      // getCusList().then(result => {
      //   //這邊寫入 尚未加入家戶的客戶 List
      // })
      this.panelOpen('addFamilyModalOpen')
    },
    selectFamily(){
      
      this.panelOpen('addFamilyModalOpen')
    },
    removeMember(i, data) {
      this.currentDelIndex = i
      this.currentWarningType = 'delFamily'
      this.warningData[this.currentWarningType].message = `您是否確認要刪除家戶成員 <a class="link text-underline">${data.name}<a/> ，若確定刪除，則<a class="link text-underline">${data.name}<a/> 需重新歸戶。`
      this.panelOpen('warningModalOpen')
    },
    warningSubmit () {
      let fn = this.warningData[this.currentWarningType].submit
      this[fn]()
      this.panelClose('warningModalOpen')
    },
    confirmClearInsurance (insurance, hasSub) {
      if(!hasSub) {
        this.currentWarningType = 'confirmDelOtherNoSUb'
      } else {
        this.warningData.confirmDelOther.message = `您是否確認要刪除此筆主約「<b>${insurance.name}</b> 」？`
        this.currentWarningType = 'confirmDelOther'
      }
      this.panelOpen('warningModalOpen')
    },
    delFamily() {
      this.customer.family.splice(this.currentDelIndex, 1)
    },
    initFamily() {
      this.recordOrResetFamily()
      this.familyEditing = true
      
      let item = [{
        link: 1,
        name: this.customer.info.name,
        gender: 0,
        identity: 'A223456789',
        role: '戶長',
        birthday: '19600809',
        currentCustomer: true
      }]
      this.customer.info.familyLeader = true
      this.customer.family = item
    },
    submitAddFamily (item) {
      
      this.panelClose('addFamilyModalOpen')
    },
    submitFamily(f) {
      // add family item from api
      this.recordOrResetFamily()
      this.familyEditing = true
      let item = [
        {
          link: 1,
          name: '陳怡君',
          gender: 0,
          identity: 'A223456789',
          role: '戶長',
          birthday: '19600809'
        },
        {
          name: '王大進',
          gender: 1,
          identity: 'A223456789',
          role: '配偶',
          birthday: '19600809'
        },
        {
          name: '王潔心',
          gender: 0,
          identity: 'A223456789',
          role: '子女',
          birthday: '19600809'
        },
        {
          name: '簡沛文',
          gender: -1,
          identity: 'A223456789',
          role: '其他',
          birthday: '19600809'
        },
        {
          link: 1,
          name: '陳潔妮',
          gender: -1,
          identity: 'A223456789',
          role: '其他',
          birthday: '19600809'
        },
        {
          name: '端木王大進',
          gender: -1,
          identity: 'A223456789',
          role: '其他',
          birthday: '19600809'
        }
      ]

      item.push({
        link: 1,
        name: this.customer.info.name,
        gender: 0,
        identity: 'A223456789',
        role: '其他',
        birthday: '19600809',
        currentCustomer: true
      })
      this.customer.info.familyLeader = false
      this.customer.family = item
      this.panelClose('familyModalOpen')
    },
    beforeClearFamily () {
      this.currentWarningType = 'clearFamily'
      this.warningData.clearFamily.message = `您是否確認要刪除以 <b>${customer.info.name}</b> 為戶長之家戶，若確定刪除，則原家戶成員皆須重新歸戶。`
      this.panelOpen('warningModalOpen')
    },
    clearFamily () {
      this.recordOrResetFamily()
      this.familyEditing = true
      this.customer.family = []
      this.customer.info.familyLeader = false
      this.panelClose('warningModalOpen')
    },
    recordOrResetFamily (reset) {
      if(reset) {
        this.customer.info.familyLeader = this.oldFamily.leader
        this.customer.family = this.oldFamily.family
      } else {
        this.oldFamily = {
          leader: window._.clone(this.customer.info.familyLeader),
          family: window._.clone(this.customer.family)
        }
      }
    },
    cancelFamilyEdit () {
      this.recordOrResetFamily(true)
      this.familyEditing = false
    },
    becomeLeader () {
      this.panelClose('warningModalOpen')
      this.familyEditing = false
    },
    saveFamily () {
      this.panelClose('warningModalOpen')
      this.familyEditing = false
    },
    submitFamilyEdit () {
      if (!this.oldFamily.leader && this.customer.info.familyLeader) {
        this.warningData.becomeLeader.message = `是否確認要修改<b>${this.customer.info.name}</b> 的家戶關係？`
        this.currentWarningType = 'becomeLeader'
      } else {
        this.warningData.saveFamily.message = `是否確認要修改<b>${this.customer.info.name}</b> 的家戶關係？`
        this.currentWarningType = 'saveFamily'
      }
      this.panelOpen('warningModalOpen')
    },
    editFamily () {
      this.recordOrResetFamily()
      this.familyEditing = true
    },
    routeToCustDetail(id, csid) {
      this.updateCustID(csid)
      localStorage.CustID = csid
      this.$router.push({path: `/detail/CustID/tab/1`})
    },
    initCustomerDetail2() {
      this.loading_1 = true
      getFamilyMembers(this.$store.state.userInfo.id, this.CustInfo.ID).then(familyResult => {
        if(familyResult.header && familyResult.header.status === '200' && familyResult.body.familymembers) {
          if(familyResult.body.familymembers.length > 0 && familyResult.body.familymembers[0].householderid == this.CustInfo.ID) {
            this.updateCustFamilyLeader = true
          }
          let memList = familyResult.body.familymembers.map((ele, index) => {
            return {
              link: index + 1,
              birthday: ele.birthday,
              guid: ele.guid,
              householderid: ele.householderid,
              name: ele.name,
              personalid: ele.personalid,
              relation: ele.relation,
              sex: Number(ele.sex) || 0,
            }
          })
          memList.sort((a, b) => {
            return a.relation < b.relation ? -1 : 1
          })
          this.familyMemList = memList
        } else {
          this.familyMemList = []
        }
        this.loading_1 = false
      })
    }
  },
  beforeMount () {
    let routeNumber = (this.$route.name.split(/-/g)).reverse()
    this.currentTab = Number(routeNumber[0])
  },
  async mounted () {
    this.initCustomerDetail2()
  }
}
</script>
