              <table id="new-group-list" class="table datatable insurance-green">
                <thead>
                  <tr>
                    <th :width="Math.floor(344 * 0.28) + 'px'"
                    @click="sortArray('selected','name')">姓名</th>
                    <th :width="Math.floor(344 * 0.30) + 'px'"
                    @click="sortArray('selected','identity')">身分證</th>
                    <th :width="Math.floor(344 * 0.25) + 'px'"
                    @click="sortArray('selected','birthday')">生日</th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                  v-for="(item, i) in selectedFamily"
                  :key="`familiy-item-${i}`">
                    <td :width="Math.floor(344 * 0.28) + 'px'">{{item.name}}</td>
                    <td :width="Math.floor(344 * 0.30) + 'px'">{{item.identity}}</td>
                    <td :width="Math.floor(344 * 0.25) + 'px'">{{item.birthday | dateFormat}}</td>
                    <td>
