  }
]

export default {
  name: 'AddFamilyModal',
  props: {
    open: {
      type: Boolean,
      default: false
    },
    customer: {
      type: Object,
      default: () => {
        return {
          name: ''
        }
      }
    }
  },
  watch: {
    'open': 'modalChange'
  },
  data () {
    return {
      keyword: '',
      customerTable: null,
      selectedTable: null,
      selectedFamily: [],
      families: families,
      unselectedSort: true,
    }
  },
  methods: {
    addGroup(index){
      this.selectedFamily.push(this.families.splice(index,1)[0])
      this.sortArray()
    },
    removeGroup(index){
      this.families.push(this.selectedFamily.splice(index,1)[0])
      this.sortArray()
    },
    // 排序陣列
    sortArray(arrayName,arrayPorp = 'name'){
      if(arrayName === 'unselected'){
        if(this.unselectedSort && this.unselectedItem === arrayPorp){
          this.unselectedSort = !this.unselectedSort
          this.families = this.families.sort(function(a, b){return (b[arrayPorp] >= a[arrayPorp])? 1:-1 ;})
        }else{
          this.unselectedItem = arrayPorp
          this.unselectedSort = true
          this.families = this.families.sort(function(a, b){return (a[arrayPorp] >= b[arrayPorp])? 1:-1 ;})
        }
      }else if(arrayName === 'selected'){
        if(this.selectedSort && this.selectedItem === arrayPorp){
          this.selectedSort = !this.selectedSort
          this.selectedFamily = this.selectedFamily.sort(function(a, b){return (b[arrayPorp] >= a[arrayPorp])? 1:-1 ;})
        }else{
          this.selectedItem = arrayPorp
          this.selectedSort = true
          this.selectedFamily = this.selectedFamily.sort(function(a, b){return (a[arrayPorp] >= b[arrayPorp])? 1:-1 ;})
        }
      }else{
        this.families = this.families.sort(function(a, b){return (a[arrayPorp] >= b[arrayPorp])? 1:-1 ;})
        this.selectedFamily = this.selectedFamily.sort(function(a, b){return (a[arrayPorp] >= b[arrayPorp])? 1:-1 ;})
      }
    },
    refreshData() {
      this.families = this.filterOutById(this.custsOfAgnt, this.custsOfGrp)
      this.selectedFamily = this.custsOfGrp
      this.sortArray()
    },
    doSearch () {
      if(this.keyword.trim() != '') {
        let searchCusts = this.custsOfAgnt.filter(item => item.name.includes(this.keyword) || item.identity.includes(this.keyword))
        this.families = this.filterOutById(searchCusts, this.selectedFamily)
      } else {
        this.families = this.filterOutById(this.custsOfAgnt, this.selectedFamily)
      }
    },
    modalChange (value) {
      
    }
  },
  mounted () {
    
  }
}
</script>

